.wb-Contentmap-container {
  margin: auto;
  display: flex;
  flex-direction: row;
  max-width: 1024px;
  flex-wrap: wrap;
  
}
.wb-Contentmap-container a {
  text-decoration: none;
  margin-left: 10px;
  font-family: Inter;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  
}
.wb-Contentmap-container a:nth-child(2n) {
    color: #5C6874;
}
.wb-Contentmap-container a:nth-child(2n-1) {
    color:#727D87;
}

